/* src/pages/Newspage/News.css */

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

.news-container {
    max-width: 80%;
    margin: 0 auto;
    font-family: 'Lato', sans-serif;
    animation: fadeIn 1s ease-in-out;
}

.news-item {
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: row;
    margin-bottom: 20px;
    padding: 10px;
    background-color: rgb(0, 0, 0, 0.40);
    box-shadow: 0 0 10px rgba(28, 9, 50, 1);
    transition: all 0.3s ease;
}

.news-item:hover {
    transform: scale(1.02);
}

.news-item img {
    max-width: 500px;
    height: auto;
    margin-right: 50px;
}

.news-item h3 {
    margin-top: 10px;
    color: #ffffff;
    font-size: 25px;
}

.news-item p {
    color: #a3a3a3;
    font-size: 20px;
}

.news-item a {
    max-width: 100%;
    display: block;
    margin-top: 10px;
    color: #ffffff;
    text-decoration: none;
    cursor: pointer;
    padding: 20px;
    background-color: #8A36EE;
    text-align: center;
    font-size: 20px;
    font-weight: 700;
}

.news-item a:hover {
    box-shadow: 0px 0px 10px rgb(101, 18, 195);
}