@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

* {
    border: none;
    outline: none;
    background: none;
    text-decoration: none;
}

.film-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    background-color: rgb(0, 0, 0, 0.40);
    box-shadow: 3px 2px 55px rgba(28, 9, 50, 1);
}

.film-block-content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    box-sizing: border-box;
    max-width: 500px;
    flex-basis: calc(33.33% - 15px);
    margin: 15px;
    transition: all 0.3s ease;

}

.film-block-content:hover {
    transform: scale(1.02);
}

.film-block-content img {
    width: 100%;
    box-shadow: 3px 2px 10px rgba(28, 9, 50, 1);
    transition: all 0.3s ease;
    cursor: pointer;
}

.film-block-content img:hover {
    transform: scale(1.02);
}

.film-block button {
    background-color: rgb(138, 54, 238, 0.25);
    height: 60px;
    margin-top: 12px;
    color: white;
    font-family: 'Lato', sans-serif;
    cursor: pointer;
    transition: all 0.3s ease;
}

.film-block button:hover {
    box-shadow: 0px 0px 10px rgb(101, 18, 195);
}

/* Неактивная ссылка */
.button-link h2 {
    color: rgb(248, 248, 248);
    text-decoration: none;
}

/* Активная ссылка */
.button-link.active {
    color: red;
    /* Дополнительные стили для активной ссылки */
}